/* Do not remove the comments below. It's the markers used by wiredep to inject
   sass dependencies when defined in the bower.json of your dependencies */
.browsehappy {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

/* Do not remove the comments below. It's the markers used by gulp-inject to inject
   all your sass files automatically */
.content {
  padding: 50px;
}

.navbar {
  position: relative;
  min-height: 50px;
  margin-bottom: 20px;
  background-color: #222;
}
.navbar > a {
  float: left;
  color: lightgrey;
  padding: 15px 15px;
  font-size: 18px;
  line-height: 20px;
  height: 50px;
}
.navbar ul {
  float: left;
  padding: 0;
  margin: 0;
}
.navbar ul li {
  float: left;
  position: relative;
  display: block;
  cursor: pointer;
}
.navbar ul li a {
  color: white;
  position: relative;
  display: block;
  padding: 15px;
  line-height: 20px;
}
.navbar ul li.active a {
  background-color: black;
}



